import React, { Component } from 'react'
import Btn from './common/Button'
import Btn2 from './common/Button2'



export class Calc extends Component {
    state = {
        btns: [
            { value: 0 },
            { value: 1 },
            { value: 2 },
            { value: 3 },
            { value: 4 },
            { value: 5 },
            { value: 6 },
            { value: 7 },
            { value: 8 },
            { value: 9 },

            { value: "C" },
            { value: "+/-" },
            { value: "%" },
            { value: "/" },
            { value: "*" },
            { value: "-" },
            { value: "+" },
            { value: "=" },
            { value: "." }
        ]
    }
    render() {
        return (
            <div className="calc">
                <div>Calc</div>
                <div className="btns">
                    {this.state.btns.map(item => <Btn key={item.value} value={item.value} />)}
                </div>
            </div>
        )
    }
}


